<template>
    <el-row :gutter="18">
      <el-col :span="6">
        <el-card>
          <div slot="header">
            <span style="line-height: 20px; font-weight: 600;"><i class="el-icon-fa-pencil-square-o"></i> 添加新的分类</span>
          </div>
          <el-form ref="form" :model='newModel'>
            <el-form-item label="输入分类名称">
            <el-input v-model="newModel.name" placeholder="输入分类名称">
            </el-input>
            </el-form-item>
            <el-form-item label="可选上级分类">
              <el-select v-model="newModel.parentId" placeholder="可选择上级分类">
                  <el-option
                    v-for="item in categories"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
            </el-form-item>
              <el-button type="success" @click.prevent="addCategory" :disabled="newModel.name===''">确定添加</el-button>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="18">
        <el-card>
          <div slot="header">
            <span style="line-height: 20px; font-weight: 600;"><i class="el-icon-fa-list"></i> 已有分类</span>
          </div>
          <div class="nested-list"  v-model="categories">
               <template v-for="(item, index) in categories">
                 <li class="nested-handle" :key="item.id"><i v-if="'children' in item" class="el-icon-fa-angle-down"></i> {{item.name}}
                  <span class="btn-group pull-right">
                    <a class="text-muted" @click.prevent="onEdit(item.id, item.name)"><i class="el-icon-fa-edit"></i></a>
                    <a class="text-muted" @click.prevent="onDelete(item.id)"><i class="el-icon-fa-trash"></i></a>
                  </span>
                </li> 
                 <ol class="nested-list" :key="index">
                   <li class="nested-child nested-handle" v-for="(child, index) in item.children" :key="item.id">{{child.name}} --当前文章数（{{child.posts_count}}）
                    <span class="btn-group pull-right">
                      <a class="text-muted" @click.prevent="onEdit(child.id, child.name)"><i class="el-icon-fa-edit"></i></a>
                      <a class="text-muted" @click.prevent="onDelete(child.id)"><i class="el-icon-fa-trash"></i></a>
                    </span>
                   </li>
                 </ol>
               </template>
          </div>
        </el-card>
      </el-col>
      <el-dialog title="输入要修改的栏目名称" :visible.sync="dialogFormVisible">
            <el-input v-model="updateModel.name"></el-input>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="updateCategory">确 定</el-button>
        </div>
      </el-dialog>
    </el-row>
</template>
<script>
import api from '../../plugins/axios'
export default {
  data () {
    return {
      newModel: {
        name: '',
        parentId: null
      },
      updateModel: {
        id: '',
        name: ''
      },
      dialogFormVisible: false,
      categories: [],
      categoryEnabled: []
    }
  },
  methods: {
    update (data) {
      this.categories = data
    // 获取所有的可用栏目
    // 根据设计，每个栏目下面要么划分子栏目，要么不划分子栏目，故：
    // 当有子栏目时，这些子栏目就是可用栏目，而父栏目就不可用了；没有子栏目时，该栏目也为可用
    // var hasChildren = _.filter(data, function (item) {
    //   return _.has(item, 'children')
    // }).map(function (o) {
    //   return o.children
    // })
    // var onlyParent = _.filter(data, function (i) {
    //   return !_.has(i, 'children')
    // }).map(function (p) {
    //   return p
    // })
    // this.categoryEnabled = _.merge(_.flatten(hasChildren), _.flatten(onlyParent))
    },
    async fetch () {
      this.newModel.name = ''
      let res = await api.get('wesite/category')
      if (res != null) {
        this.update(res.data)
      }
    },
    async addCategory () {
      let res = await api.post('wesite/category', this.newModel)
      if (res != null) {
        this.$message({
          message: '操作成功',
          type: 'success',
          duration: 5000
        })
        this.fetch()
      }
    },
    async deleteCategory (id) {
      let res = await api.delete('wesite/category/' + id)
      if (res != null) {
        this.$message({
          message: '操作成功',
          type: 'success',
          duration: 5000
        })
        this.fetch()
      }
    },
    async updateCategory () {
      this.dialogFormVisible = false
      let res = await api.put('wesite/category/' + this.updateModel.id, this.updateModel)
      if (res != null) {
        this.$message({
          message: '操作成功',
          type: 'success',
          duration: 5000
        })
        this.fetch()
      }
    },
    onDelete (id) {
      this.$confirm('此操作不可逆，并且在删除上级分类时会将所有下级分类一并删除，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(() => {
        this.deleteCategory(id)
      })
    },
    onEdit (id, name) {
      this.dialogFormVisible = true
      Object.assign(this.updateModel, {id: id, name: name})
    }
  },
  mounted () {
    this.fetch()
  }
}
</script>


