<template>
  <div>
    <script :id="id" type="text/plain"></script>
  </div>
</template>
<script>
  export default {
    name: 'UE',
    data () {
      return {
        id: 'StarUE_' + Math.random(10),
        instance: null
      }
    },
    props: {
      config: {}
    },
    methods: {
      init () {
        this.$nextTick(() => {
          this.instance = window.UE.getEditor(this.id, this.config)
          this.instance.addListener('ready', () => {
            this.$emit('ready', this.instance)
          })
        })
      },
      setContent (value) {
        this.editor.setContent(value || this.content || this._content || '')
      },
      execCommand (name, value, dir) {
        this.editor.execCommand(name, value, dir)
      },
      getContent (type, fn) {
        type = type || 'Content'
        return this.editor[`get${type}`](fn)
      },
      onContentChange () {
        this.editor.addListener('contentChange', function () {
          this._content = this.editor.getContent()
        }.bind(this))
      }
    },
    mounted () {
      this.init()
    },
    beforeDestroy () {
      if (this.instance !== null && this.instance.destroy) {
        this.instance.destroy()
      }
    }
  }
</script>


