<template>
    <el-row :gutter="20">
      <el-col :span="18"><UE ref="UE" @ready="ueReady"></UE></el-col>
      <el-col :span="6">
        <div class="postbox">
          <el-form ref="form" :model='post'>
            <el-form-item label="所属分类">
              <el-select v-model="post.category" placeholder="请选择分类">
                  <el-option
                    v-for="item in post.categories"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="文章状态">
              <el-radio class="radio" v-model="status" label="0">草稿</el-radio>
                <el-radio class="radio" v-model="status" label="1">发布</el-radio>
            </el-form-item>
              <el-button type="success" @click.prevent="onSubmit" :disabled="post.content===''">确定修改</el-button>
          </el-form>
        </div>
      </el-col>
    </el-row>
</template>
<script>
  import UE from '../../../components/UEditor'
  import api from '../../../plugins/axios'
  export default {
    data () {
      return {
        post: {},
        status: '0'
      }
    },
    methods: {
      async onSubmit () {
        // window.console.log(this.$refs.UE.instance.getContent())
        let data = {
          content: this.$refs.UE.instance.getContent(),
          title: document.getElementById('editor-title').value,
          status: parseInt(this.status),
          category: this.post.category
        }
        let res = await api.put('wesite/post/' + this.$route.params.id, data)
        if (res !== null) {
          this.$message('adsfasdf')
        }
      },
      editorInit () {
        window.console.log('aaa')
      },
      ueReady (instance) {
        instance.setContent('内容加载中...')
      },
      async fetch () {
        let res = await api.get('wesite/post/' + this.$route.params.id, {include: 'category'})
        if (res != null) {
          this.post = Object.assign({}, res.data)
          document.getElementById('editor-title').value = res.data.title
          this.$refs.UE.instance.setContent(res.data.content)
          if (res.data.status === '已发布') {
            this.status = '1'
          } else {
            this.status = '0'
          }
        }
      }
    },
    mounted () {
      this.fetch()
    },
    components: {
      UE
    }
  }
</script>


