import Proxy from './Proxy'

class WesiteProxy extends Proxy {
  constructor (parameters = {}) {
    super('wesite', parameters)
  }

  getCategories () {
    return this.submit('get', `${this.endpoint}/category`)
  }

  createCategory (data) {
    return this.submit('post', `${this.endpoint}/category`, data)
  }

  updateCategory (id, data) {
    return this.submit('put', `${this.endpoint}/category/` + id, data)
  }

  deleteCategory (id) {
    return this.submit('delete', `${this.endpoint}/category/` + id)
  }

  getPosts (param) {
    this.parameters = param
    return this.submit('get', `${this.endpoint}/post`)
  }

  getPost (id) {
    return this.submit('get', `${this.endpoint}/post/` + id)
  }

  createPost (data) {
    return this.submit('post', `${this.endpoint}/post`, data)
  }
}

export default WesiteProxy



