<template>
  <div>
    <el-table
        class="table-with-handler"
        :data="posts"
        stripe
        style="width: 100%">
        <el-table-column
              type="selection"
              width="20">
            </el-table-column>
        <el-table-column
          label="标题"
          class-name="td-with-handler"
          >
          <template slot-scope="scope">
            {{scope.row.title}}
            <span class="btn-group text-muted pull-right">
              <el-button type="success" style="padding:5px;" size="mini" @click.prevent="onEdit(scope.row.id)"><i class="el-icon-fa-edit"></i></el-button>
              <el-button type="danger" style="padding:5px;margin-left:5px;" size="mini" @click.prevent="onDelete(scope.row.id)"><i class="el-icon-fa-trash"></i></el-button>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="category"
          label="所属分类"
          width="120">
        </el-table-column>
        <el-table-column
          prop="updated_at"
          label="修改日期"
          width="200">
        </el-table-column>
        <el-table-column
          prop="status"
          label="文章状态"
          width="100"
          >
        </el-table-column>
      </el-table>
      <el-pagination
          layout="prev,pager,next"
          @current-change="onPageChange"
          :current-page="currentPage"
          :total="meta.total"
          :page-size="per_page">
        </el-pagination>
  </div>
</template>

<script>
  import api from '../../../plugins/axios'
  export default {
    data () {
      return {
        posts: [],
        currentPage: 1,
        per_page: 15,
        meta: []
      }
    },
    methods: {
      onPageChange (page) {
        this.currentPage = page
        this.getData()
      },
      async getData () {
        let res = await api.get('wesite/post', {currentPage: this.currentPage, perPage: this.per_page})
        this.posts = res.data
        this.meta = res.meta
      },
      onEdit (id) {
        this.$router.push({path: '/wesite/post/edit/' + id})
      },
      onDelete (id) {
        this.$emit('deletePost', id)
      }
    },
    mounted () {
      this.getData()
    }
  }
</script>

<style>
  .td-with-handler {
    cursor: pointer;
  }
  .table-with-handler .td-with-handler:hover .btn-group {
    visibility: visible;
    opacity: 1;
    transition: opacity .3s linear;
  }
  .table-with-handler .btn-group {
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s linear;
  }
</style>


